@import url('function.less');
html,body { background: #eceff1;font-size: 14px;color: #333;}
@blue:#005bac;@orange:#f48235;@green:#0db26d;@black:#333333;@gray:#999999;
img{border: none;outline: none;vertical-align: middle;}
.mt15 {margin-top: 15px;}
.mb15{margin-bottom: 15px;}
.mt20 {margin-top: 20px;}
.mb20 {margin-bottom: 20px;}
.pt10{padding-top: 10px;}
.pb10{padding-bottom: 10px;}

.clr-orange{color: @orange;}
.clr-blue{color: @blue;}
.clr-gray{color: @gray;}

.date {.txt-ver(40px);font-size: 12px;}
.iframe-weather {padding: 9px 0;}
.fn {.txt-ver(40px); .tr;
    a {.d-ib;color: @blue;font-size: 12px;}
    s { font-family: simsun; padding: 0 10px; color: @gray;}
    .tx {.d-ib;.clearfix;.pos(r);z-index: 2;.fr;
        b {.d-ib;.wh(28px);line-height: 28px;margin-right: 10px;
            img {.wh(100%);border-radius: 50%;overflow: hidden;}
        }
        span{.d-ib; .pos(r);padding-right: 15px;color:@blue;
            &:after{.pos(a);.triangle-b(@w:6px, @color:#ccc); right:0; top:50%; margin-top: -3px;content: "";}
        }
        .tx-menu {.pos(a); right: 0; top:40px; background: #fff; width: 90px; padding: 0 10px; box-shadow: 0 1px 2px 2px rgba(0,0,0,0.2); border-radius: 3px;.d-n;
            a {.d-b;border-bottom: 1px solid #eee;.tl;color: @black;}
            i {.d-ib;.fl;width: 25px; height: 40px;}
            .clbd {border-bottom: none;}
            &:before {.pos(a); .triangle-t(@w:5px, @color:#fff); left: 50%; top:-10px; content: "";}
        }
    }
    .sch {.d-ib; width: 170px;.txt-ver(26px); border: 1px solid #ddd; border-radius: 5px;.clearfix; background: #fff;vertical-align: middle; margin-right: 10px;
        input {height: 26px; line-height: 26px;.fl; border:none; background: transparent;width: 120px; padding-left: 5px;}
        button {background: url(../img/ps1/icon/ico-sch.png); width: 29px;height: 26px; cursor: pointer;border: none;.fr;}
    }
}
.ico{
    &.ico-star{ background: url(../img/ps1/icon/ico-star.png) no-repeat left center; padding-left: 15px;}
    &.ico-wenti { background: url(../img/ps1/icon/ico-wenhao.png) no-repeat left center; padding-left: 15px;}
    &.ico-denglu{background: url(../img/ps1/icon/ico-denglu.png) no-repeat left center; padding-left: 17px;}
    &.ico-username {background: url(../img/ps1/icon/ico-username.png) no-repeat 10px center; padding-left: 42px;}
    &.ico-password {background: url(../img/ps1/icon/ico-password.png) no-repeat 13px center; padding-left: 42px;}
    &.ico-email2 {background: url(../img/ps1/icon/ico-email2.png) no-repeat 13px center; padding-left: 42px;}
    &.ico-blank {padding-left: 42px;}
    &.ico-tel {background: url(../img/ps1/icon/ico-lianxifangshi.png) no-repeat left center;padding-left: 15px;}
    &.ico-weixin {background: url(../img/ps1/icon/ico-weixin.png) no-repeat left center;padding-left: 19px;}
    &.ico-add {background: url(../img/ps1/icon/ico-add.png) no-repeat left center;padding-left: 18px;}
    &.ico-fav {background: url(../img/ps1/icon/ico-fav1.png); width: 23px; height: 21px;.d-ib;overflow: hidden;
        &.on {background: url(../img/ps1/icon/ico-fav2.png);}
    }
}
.header { background: @green;height: 70px;
    .g-wrap {.pos(r);height: 70px;}
    .logo {.pos(a); top:7px; left: 0;}
    .set-home {.pos(a); left: 210px; top:27px;}
    ul {.pos(a); right:0; top:0;}
    li{.fl;background: url(../img/ps1/icon/ico-line.png) no-repeat right center; padding: 0 3px;
        a {font-size: 16px; color: #fff;.txt-ver(74px);.d-b; transition: 0.2s background-color; padding: 0 17px;}
        a:hover,a.active {background: @orange;}
    }
    li:nth-last-of-type(1){padding-right: 0; background: none;}
}
.keyword {.tc;font-size: 14px;padding: 15px 0;
    a {color: @orange;}
    a:hover{text-decoration: underline;}
    s{font-family: simsun; font-size: 12px; color: @gray;padding: 0 5px;}
    &.s1 {.tl; padding: 0; padding-bottom: 15px; padding-top: 10px;
        a {color: @green;}
        s {color:#ddd}
    }
}
.baidu {vertical-align: middle;.tc;
    .baidu-key{.txt-ver(38px); border:1px solid #bcbcbc;background: #fff;padding: 0 20px; vertical-align: middle; width: 495px;}
    .baidu-btn{.txt-ver(40px);background: #3388ff; color:#fff; padding: 0 20px; font-size: 16px;border: none; cursor: pointer; vertical-align: middle;}
}
.box {border: 1px solid #ddd; background: #fff;
    .box-inner {padding: 0 14px;
        &.s1 {padding: 0 20px 20px 20px;}
    }
    &.s1 {background:#fff url(../img/ps1/bg/bg-person.png) repeat-y left top;}
}
.side-nav {.clearfix; padding: 10px 0 10px 15px;
    a {width: 24.9%;.d-ib;.fl;font-size: 14px; line-height: 36px;
        b {.d-ib; width: 25px; height: 36px;.fl;}
    }
    a:hover{color: @green;}
}
.img-txt {.pos(r);.d-b;
    span {.pos(a); left: 0; bottom:0;right:0;padding: 10px; color: #fff; background: url(../img/ps1/bg/black60.png);}
}
.tab-name { margin-left: -1px;border-bottom: 1px solid #ddd;border-left:1px solid #ddd;
    li {width: 16%;.fl;.tc;font-size: 16px;border: 1px solid #fff;border-top:none;border-bottom: none;.pos(r);margin: -1px 0 0 px;
        a {.txt-ver(49px);color: @gray;}
        a:hover{color: @green;}
    }
    .current {border: 1px solid #ddd;border-top:none;border-bottom: none;padding-bottom: 1px;margin-bottom: -1px;background: #fff;
        a {color: @green;}
    }
}
.btn{
    //添加删除网址
    &.ico-set {border: 1px solid @orange; padding: 0px 10px 0px 30px; .txt-ver(28px); background: url(../img/ps1/icon/ico-set.png) no-repeat 10px center; color: @orange; .font-wn; font-size: 12px; 
        &:hover{border:1px solid @green;}
    }
    //我要发布
    &.ico-woyaofabu {border: 1px solid @orange; padding: 0px 10px 0px 30px; .d-ib; .txt-ver(28px); background: url(../img/ps1/icon/ico-woyaofabu.png) no-repeat 10px center; color: @orange; .font-wn; font-size: 12px;border-radius:14px;  
        &:hover{border:1px solid @green;}
    }
    //查看更多
    &.see-more{border: 1px solid @orange; .d-ib; padding: 15px 85px; color: @orange; color: @orange;margin-top: 20px;
        &:hover{background: @orange; color: #fff;}
    }
    &.btn-c5 {border: 1px solid @orange; color: @orange; padding: 10px 40px; background: transparent;font-size: 18px;border-radius: 3px;
        &:hover{background: @orange; color: #fff;}
    }
    &.btn-middle {padding: 8px 20px;font-size: 14px;}
    &.ico-none {border: 1px solid @orange; padding: 0px 20px 0px 20px; .d-ib; .txt-ver(28px); color: @orange; .font-wn; font-size: 12px;border-radius:14px;  
        &:hover{background: @orange; color: #fff;}
    }
    &.s1 {.btn.ico-woyaofabu; background: none; padding: 0px 20px; border-radius:2px; 
        &:hover{background: @orange; color: #fff;}
        &.big {font-size: 16px;padding: 5px 30px;}
    }
    &.s2 {border: 1px solid #fff; .d-ib; padding: 6px 20px; font-size: 16px; color: #fff;border-radius: 3px;}
    &.ico-xihuan { border: 1px solid @orange; padding: 8px 20px 8px 40px; background: url(../img/ps1/icon/ico-xihuan1.png) no-repeat 15px center; border-radius: 30px;.d-ib;font-size: 16px;
        s{font-family: simsun;color: @gray; font-size: 12px; padding: 0 10px;}
        &.active {background-image: url(../img/ps1/icon/ico-xihuan2.png);}
    }
    &.full {background: #fafafa;color: @orange; padding: 10px;.d-b;.tc;}
}
.url-list {
    a {display:inline-block;width:130px;float:left;line-height:36px;}
    a:before{ display: inline-block;margin: 0 6px 2px 0px;border: 2px solid;vertical-align: middle;content: '';border-width: 2px;color: #ddd;margin-bottom: 5px;}
    a:hover {color:#fc6621;text-decoration:underline;}
    .favico { background-repeat: no-repeat; background-position: left center; padding-left: 20px; width: 110px;}
    hr {height: 1px; border:none; background: #eee;margin: 15px 0;}
}
.title {
    &.s1{font-size: 16px; .txt-ver(49px);.pos(r); padding: 0 20px;}
    &.s2{.title.s1;border-bottom: 1px solid #eee; padding: 0 20px;
        .btn.ico-set{.pos(a); right:20px; top:10px;}
        s {color: @green; font-size: 12px;.pos(r);padding-left: 10px;margin-left: 8px;
            &:before{border: 2px solid #ccc;.pos(a); left: 0; top:6px;content: "";}
        }
    }
    &.s3 {.tc; color: @orange;.font-wn;padding: 10px 0;
        a {margin: 0 10px; color: @orange;}
        a:hover {text-decoration: underline;}
    }
    &.s4{.title.s2; padding: 0;}
    &.s5{font-size: 14px;.font-wn;padding: 20px 0;}
    &.s6 {.title.s2;.pos(r); padding: 0;
        span {color: @gray;font-size: 14px; padding-left: 10px;.font-wn;}
        .btn{.pos(a); right:20px; top:10px;}
    }
    
}
.url-list2 {
    h4 {float:left;padding-left:20px;font-weight:normal;width:80px;.tl;}
    h4 a {color: @green; .pos(r);.d-b; width: 4rem;}
